@import "ScssHelpers/helpers-all";

.pet_food__step_frame_title {
	margin-bottom: 3.6rem;
	text-align: center;

	a,
	strong {
		position: relative;
		display: inline-block;
		margin: 0 0.8rem;

		color: $black;
		font-weight: 500;
		letter-spacing: -0.016em;

		@include bp($point_4, min) {
			font-size: 3.2rem;
		}

		@include bp($point_4 - 1) {
			font-size: 1.8rem;
		}

		&::after {
			position: absolute;
			bottom: 0;
			left: 0;
			display: block;
			width: 100%;
			height: 3px;
			content: "";

			background-color: $gold_2;

			transition: background 0.3s ease;
		}

		// &:not(:first-child) {
		// 	margin-right: 0;
		// 	// margin-right: 0 0 0 .8rem;
		// }

		// &:not(:last-child) {
		// 	margin-left: 0;
		// 	// margin-left: 0 .8rem 0 0;
		// }

		&:first-child {
			margin-left: 0;
		}

		&:last-child {
			margin-right: 0;
		}
	}

	a {
		cursor: pointer;
		transition: color 0.3s ease;

		&:hover,
		&:focus {
			text-decoration: none;
			color: $gold_2;

			&::after {
				background-color: $brown_3;
			}
		}
	}

	@include bp($point_4, min) {
		font-size: 2.4rem;
	}

	@include bp($point_4 - 1) {
		font-size: 1.6rem;
	}
}

.pet_food__step_frame_btn_w {
	text-align: center;
}

.pet_food__step_frame {
	padding: 4rem 4rem 5.5rem;

	border-radius: 0.8rem;
	border: 1px solid #f2e4d1;

	&:not(:last-child) {
		margin-bottom: 4rem;
	}
}
