@import 'ScssHelpers/helpers-all';

.pet_food__content {
	text-align: center;
}

.pet_food__content h4 {
	@include h4;

	margin-bottom: 2.6rem;
}

.pet_food__input_w {
	margin: 0 auto;

	@include bp($point_2, min) {
		max-width: 60rem;
	}
}

// ------------------- step 2

.pet_food__head {
	margin-bottom: 3.4rem;
}

.pet_food__title {
	@include h3;

	margin-bottom: .9rem;
	letter-spacing: -0.016em;
	text-align: center;
}

.pet_food__descr {
	@include text;

	max-width: 91rem;
	margin: 0 auto;
	text-align: center;
	line-height: 150%;
}

// ------------------- step 3

.pet_food__3_box {
	width: 100%;
	max-width: 122.4rem;
	margin: 0 auto;
}

.pet_food__btn_w {
	text-align: center;
}

.pet_food__3_list_w {
	margin-bottom: 4.4rem;
}

.pet_food__3_list {
	@include flex_block(row, wrap, flex-start, stretch);

	margin: 0 -2.4rem -2.4rem 0;
}

.pet_food__3_item {
	width: 25%;

	padding: 0 2.4rem 2.4rem 0;
}

// ------------------- step 4


// ------------------- step 8
.pet_food__list {
	@include flex_block(row, wrap, flex-start, stretch);

	margin: 0 -2.4rem -2.4rem 0;
}

.pet_food__list_item {
	width: 25%;

	padding: 0 2.4rem 2.4rem 0;

	text-align: left;

	.pet_food__list--columns_3_mod & {
		width: 33.33%;
	}
}
